<template>
    <div class="header-container">
        <div class="header-content-mobile" v-if="!isPc">
            <div class="header-menu">
                <a class="a-logo" @click="enterHomePage()"></a>
                <button class="hamburger hamburger--squeeze" type="button" @click="openMenu"
                        :class="{'is-active': isOpen}">
                    <span class="hamburger-box">
                      <span class="hamburger-inner"></span>
                    </span>
                </button>
            </div>
            <div class="menu-content" id="menu-content" :style="{display: isOpen ? 'flex': 'none'}">
                <a class="header-link" @click="switchCatalogue(index)" v-for="(item, index) in data"
                   :key="index">{{ item.name }}</a>
                <a-radio-group class="lang-switch" v-model="langMb">
                    <a-radio :value="item.value === lang ? 1 : 0" v-on:click="switchLang(item)"
                             v-for="(item,index) in langList" :key="index">{{ item.key }}
                    </a-radio>
                </a-radio-group>
                <p class="contact-us" @click="openContact()">{{ $t('help.Button') }}</p>
            </div>
        </div>
    </div>
</template>

<script>
import {AU_UUID, AU_HMAC, AU_SELECTED_SERVICE} from "@/store/Global";
import {TweenMax} from 'gsap';

export default {
    name: "Header",
    data() {
        return {
            serviceChildList: [{key: '登出服務', path: 'logout'}],
            langList: [{key: '繁体中文', value: 'zh-TW'}, {key: '简体中文', value: 'zh-CN'}, {key: 'English', value: 'en-US'}],
            lang: this.$i18n.locale,
            router: this.$route.path,
            isLogin: localStorage.getItem('uuid') && localStorage.getItem('hmac'),
            selectedService: JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)),
            isPc: this.$isPc(),
            isOpen: false,
            langMb: 1,
        }
    },
    props: {
        data: {},
    },
    watch: {
        data: {
            immediate: true,
            handler(value) {
                this.data = value;
            }
        },
        $route(to) {
            if (this.router !== to.path) {
                this.router = to.path;
            }
            //console.log('to',to);
        },
    },
    mounted() {
        this.isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
        if (this.isLogin) {
            if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                this.serviceChildList = [{key: '管理賬戶', path: 'user/account', name: 'manageAccount'}, {
                    key: '管理賬單',
                    path: 'invoice',
                    name: 'manageBill'
                }, {key: '登出服務', path: 'logout'}]
            } else {
                const that = this;
                this.$request.auGetAllServices('', '', function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        if (data.data.jjService && data.data.jjService.length > 0) {
                            localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(data.data.jjService[0]));
                            that.serviceChildList = [{
                                key: '管理賬戶',
                                path: 'user/account',
                                name: 'manageAccount'
                            }, {key: '管理賬單', path: 'invoice', name: 'manageBill'}, {key: '登出服務', path: 'logout'}]
                        }
                    }
                });
            }
        }
    },
    methods: {
        switchCatalogue(index) {
            this.openMenu();
            this.$emit("switchCatalogue", index);
        },
        openContact() {
            this.openMenu();
            this.$emit("isMBOpenContact", true);
        },
        switchLang(lang) {
            if (this.lang !== lang.value) {
                this.lang = lang.value;
                this.$emit('switchLang', true);
                this.$setupLang(lang.value);
            }
            if (!this.isPc) {
                this.isOpen = false
            }
        },
        enterHomePage() {
            if (this.isPc || (!this.isPc && !(this.router.includes('order/') || this.router.includes('user/invoice')))) {
                if (this.$route.name !== 'product') {
                    if (!(this.$route.name === 'home')) {
                        this.$router.push({
                            path: '',
                            name: 'home',
                        });
                    }
                }
            } else if (!this.isPc && (this.router.includes('order/') || this.router.includes('user/invoice'))) {
                this.$router.back(-1);
            }
            if (!this.isPc) {
                this.isOpen = false;
            }
        },
        openMenu() {
            if (this.isOpen) {
                const that = this;
                TweenMax.fromTo(
                    document.getElementById('menu-content'),
                    0.3,
                    {right: '0'},
                    {
                        css: {right: '-100%'}, ease: Power0.easeNone, onComplete: function () {
                            that.isOpen = false
                        }
                    }
                )
            } else {
                this.isOpen = true;
                TweenMax.fromTo(
                    document.getElementById('menu-content'),
                    0.3,
                    {right: '-100%'},
                    {right: '0', ease: Power0.easeNone}
                )
            }

        }
    }
}
</script>
<style scoped src="../assets/css/hamburgers.css"></style>
<style lang="scss">
.logout {
    height: 30px;
    width: 100px;
    font-size: 16px;
    cursor: pointer;
    text-align: center;
    margin: 0px;
    border: 0px solid #E4EAF0;

    &:hover {
        color: #3963EF;
        border-color: #3963EF;
    }
}

.lang-item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 48px;
    width: 150px;
    font-size: 16px;
    cursor: pointer;
    text-align: center;
    background-color: #FFFFFF;
    margin: 0px  -16px;
    color: #484652;

    &:hover {
        background-color: #F2F6FA;
    }
}

.lang-item:first-child {
    margin-top: -12px;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}

.lang-item:last-child {
    margin-bottom: -15px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
}

.checked-lang-item {
    background-color: #F2F6FA;
}

.header-container {
    background: #fff;
    width: 100%;

    .header-content-mobile {
        width: 100%;

        .header-menu {
            width: 100%;
            height: 1.173rem;
            margin: 0px auto;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 0.533rem;
            border-bottom: 0.013rem solid #E5E9ED;

            .a-logo {
                width: 2.253rem;
                height: 0.587rem;
                background: url('../assets/logo-index.png') center no-repeat;
                background-size: contain;
            }

            .a-back {
                width: 0.493rem;
                height: 0.493rem;
                background: url('../assets/header/ic-header-back.svg') center no-repeat;
                background-size: contain;
            }

            .hamburger {
                padding: 0;

                &:focus {
                    outline: none;
                }
            }
        }

        .menu-content {
            position: absolute;
            width: 100%;
            height: 100%;
            background: #fff;
            display: flex;
            flex-direction: column;
            right: -100%;
            z-index: 100;
            //top: 2rem;
            padding: 0.533rem 0 0 1.333rem;

            .header-link {
                margin: 0;
                display: flex;
                align-items: center;
                font-size: 0.427rem;
                font-weight: 400;
                height: 1.56rem;
                text-align: left;
                color: #242C45;
                border-bottom: 0.013rem solid #E5E9ED;
            }

            a, .subscribe-btn {
                margin: 0 0.533rem;
                font-size: 0.427rem;
                font-weight: 400;
            }

            .subscribe-btn {
                margin: 0.48rem 0 0 0;
                height: 0.96rem;
                width: 2.773rem;
                color: #3963EF;
                border-color: #3963EF;

                &:hover {
                    color: #fff;
                    background-color: #3963EF !important;
                }
            }

            .a-top-line {
                margin-top: 0.48rem;
                border-top: 0.027rem solid #E5E9ED;
            }

            .lang-switch {
                //margin-top: 0.48rem;
                height: 1.56rem;
                display: flex;
                flex-direction: row;
                //border-top: 0.027rem solid #E5E9ED;
                border-bottom: 0.027rem solid #E5E9ED;
                align-items: center;

                span {
                    color: #3963EF;
                }

                .ant-radio-wrapper {
                    .ant-radio {
                        .ant-radio-inner {
                            border-color: #3963EF;

                            &:after {
                                border: 1px solid #3963EF
                            }
                        }

                    }
                }
            }

            .contact-us {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 3.387rem;
                height: 1.227rem;
                background-color: #fff;
                margin-top: 0.96rem;
                font-size: 0.427rem;
                color: #242C45;
                border: 0.013rem solid #3963EF;
                border-radius: 0.013rem;
                cursor: pointer;

                &::before {
                    /*position: relative;*/
                    /*display: inline-block;*/
                    content: '';
                    width: 0.4rem;
                    height: 0.347rem;
                    margin-right: 0.213rem;
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/contact-us-icon.svg') center no-repeat;
                    background-size: contain;
                }

                &:hover, &:active {
                    border: 0.013rem solid #3963EF;
                }
            }
        }
    }
}
</style>
