<template>
  <div class="support-container">
    <div class="support-container-pc" v-if="isPc">
      <!--<PublicHeader class="support-header" v-on:isLogout="isLogout"></PublicHeader>-->
      <a-spin v-if="isLoading" class="support-loading">
        <a-icon slot="indicator" type="loading" style="font-size: 70px" spin/>
      </a-spin>
      <div v-if="!isLoading" class="support-content">
        <div class="support-catalogue-container">
          <p class="contact-us" @click="openContact()">{{ $t('help.Button') }}</p>
          <div class="category-item" v-for="(categoryItem, categoryIndex) in catalogue" :key="categoryIndex">
            <p class="category-name" :class="{'blue-font': categoryIndex === catalogueIndex}"
               @click="openCategory(categoryIndex)">{{ categoryItem.name }}</p>
            <div v-if="categoryItem.sections.length != 1">
              <p class="section-name"
                 :class="{'blue-font': secIndex === sectionIndex && categoryIndex === catalogueIndex}"
                 v-for="(sectionItem, secIndex) in categoryItem.sections" :key="secIndex"
                 @click="openSection(categoryIndex, secIndex)">{{ sectionItem.name }}</p>
            </div>
          </div>
        </div>
        <div v-if="catalogue.length > 0" class="right-content">
          <div class="breadcrumb-search">
            <a-breadcrumb separator=">" v-if="false">
              <a-breadcrumb-item href="www.baidu.com">
                <a-icon type="home" @click="gotoHome()"/>
              </a-breadcrumb-item>
              <a-breadcrumb-item v-for="(itemName, breadIndex) in breadcrumb" :key="breadIndex"><a
                  @click="breadcrumbGoto(breadIndex)">{{ itemName }}</a>
              </a-breadcrumb-item>
            </a-breadcrumb>
            <div class="search-input">
              <a-input class="search" :placeholder="$t('help.Text')" size="large" v-model="searchText"
                       @pressEnter="searchKeyword()"/>
              <a-button class="search-btn" :loading="iconLoading" type="primary" @click="searchKeyword()"></a-button>
            </div>
          </div>
          <div v-if="showType === 0" class="other-catalogue">
            <p class="client-title">{{ selectCatalogue.name }}</p>
            <p class="client-description">{{ selectCatalogue.description }}</p>
            <div v-if="selectCatalogue.sections.length != 0" class="client-item-container">
              <div class="client-item" v-for="(section, showIndex) in selectCatalogue.sections" :key="showIndex">
                <div class="title-container" @click="openSection(catalogueIndex, showIndex)" v-if="section.isShow">
                  <i class="title-icon"
                     :class="{'signup-icon': section.id === 360004851571,
                   'client-setup-icon': section.id === 360007511651,
                   'flow-plan-icon': section.id === 360007419392,
                   'flow-fee-icon': section.id === 360007511611,
                   'payment-icon': section.id === 360007419372,
                   'android-icon': section.id === 360001781572,
                    'mac-icon': section.id === 360001770571,
                    'ios-icon': section.id === 360001779432,
                    'router-icon': section.id === 360001772931,
                    'linux-icon': section.id === 360008706572,
                    'chrome-icon': section.id === 360007419332,
                    'windows-icon': section.id === 360001720032
                   }"
                  ></i>
                  <span class="title-name">{{ section.name }}</span>
                </div>
                <div class="article-list" :class="{'article-list-open': sectionIndex === showIndex}">
                  <p class="article-name" v-for="(article, articleIndex) in getShowArticles(section, section.articles)"
                     :key="articleIndex"
                     @click="displayArticle(showIndex, articleIndex)">{{ article.name }}</p>
                </div>
                <p v-if="section.articles.length > 3" class="show-more"
                   @click="openSectionOpen(catalogueIndex, showIndex)">
                  <i class="show-more-icon"
                     :class="{'show-more-icon-close': section.showMoreTip === 'Fold'}"
                  ></i>{{ $t(section.showMoreTip === 'Fold' ? 'help.Header-status-2' : 'help.Header-status-1') }}</p>
              </div>
            </div>
          </div>
          <div v-if="showType === 1" class="articles-detail">
            <router-view/>
          </div>
          <div v-if="showType === 2" class="search-list">
            <p class="search-result-title">{{ $t('help.Title') }}"{{ searchTextBuf }}"</p>
            <p class="result-count">{{ searchResults.length }} {{ $t('help.Subtitle') }}</p>
            <div class="result-container">
              <div class="result-item" v-for="(result,searchIndex) in searchResults" :key="searchIndex">
                <p class="result-title" @click="displayFromSearch(result)">{{ result.name }}</p>
                <p class="result-detail" v-html="result.snippet" @click="displayFromSearch(result)"></p>
                <p class="result-from">{{ $t('help.Paragraph-1') }}<span
                    @click="openCategory(result.categorieIndex)">{{ result.categorieName }}</span> > <span
                    @click="openSection(result.categorieIndex, result.sectionIndex)">{{ result.sectionName }}</span></p>
              </div>
            </div>
            <p class="no-result-tips">{{ $t('help.Paragraph-2') }}<span class="contact-us"
                                                                        @click="openZw()">{{
                $t('help.Hyperlink')
              }}</span>
            </p>
          </div>
          <div v-if="showType === 3" class="contact-container">
            <router-view v-bind:channelId="mChannelId"/>
          </div>
        </div>
      </div>
      <div v-if="!isLoading" class="support-bg-left"></div>
      <!--<div class="support-bg-right"></div>-->
    </div>
    <div class="support-container-mb" v-if="!isPc">
      <SupportMbHeader id="mb-header"
                       v-bind:data="catalogue"
                       v-on:switchCatalogue="switchCatalogueMb"
                       v-on:isMBOpenContact="openContact">
      </SupportMbHeader>
      <a-spin v-if="isLoading" class="support-loading">
        <a-icon slot="indicator" type="loading" style="font-size: 70px" spin/>
      </a-spin>
      <div v-if="!isLoading" id="support-content" class="support-content">
        <div class="center-content">
          <a-breadcrumb separator=">" class="top-breadcrumb" v-if="false">
            <a-breadcrumb-item href="www.baidu.com">
              <a-icon type="home" @click="gotoHome()"/>
            </a-breadcrumb-item>
            <a-breadcrumb-item v-for="(itemName, breadIndex) in breadcrumb" :key="breadIndex"><a
                @click="breadcrumbGoto(breadIndex)">{{ itemName }}</a>
            </a-breadcrumb-item>
          </a-breadcrumb>
          <div class="search-box">
            <a-input class="search-content" :placeholder="$t('help.Text')" size="large" v-model="searchText"
                     @pressEnter="searchKeyword()"/>
            <a-button :loading="iconLoading" type="primary" @click="searchKeyword()"></a-button>
          </div>
          <div v-if="showType === 0" class="other-catalogue">
            <p class="client-title">{{ selectCatalogue.name }}</p>
            <p class="client-description">{{ selectCatalogue.description }}</p>
            <div v-if="selectCatalogue.sections.length != 1" class="client-item-container">
              <div class="client-item" :class="{'client-item-border':selectCatalogue.id === 360000728332}"
                   v-for="(section, showIndex) in selectCatalogue.sections" :key="showIndex">
                <div class="title-container" @click="openSection(catalogueIndex, showIndex)">
                  <i class="title-icon"
                     :class="{'signup-icon': section.id === 360004851571,
                   'client-setup-icon': section.id === 360007511651,
                   'flow-plan-icon': section.id === 360007419392,
                   'flow-fee-icon': section.id === 360007511611,
                   'payment-icon': section.id === 360007419372,
                   'android-icon': section.id === 360001781572,
                    'mac-icon': section.id === 360001770571,
                    'ios-icon': section.id === 360001779432,
                    'router-icon': section.id === 360001772931,
                    'linux-icon': section.id === 360008706572,
                    'chrome-icon': section.id === 360007419332,
                    'windows-icon': section.id === 360001720032
                   }"
                  ></i>
                  <span class="title-name">{{ section.name }}</span>
                  <i class="down-icon" :class="{'up-icon':sectionIndex === showIndex}"
                     v-if="selectCatalogue.id === 360000728332"></i>
                </div>
                <div class="article-list" :class="{'article-list-open': sectionIndex === showIndex}"
                     v-if="(selectCatalogue.id === 360000728332 && sectionIndex === showIndex) || selectCatalogue.id !== 360000728332">
                  <p class="article-name"
                     v-for="(article, articleIndex) in getShowArticles(sectionIndex === showIndex, section.articles)"
                     :key="articleIndex"
                     @click="displayArticle(showIndex, articleIndex)">{{ article.name }}</p>
                </div>
              </div>
            </div>
            <div v-if="selectCatalogue.sections.length === 1" class="client-item-container">
              <div class="client-item" v-for="(section, showIndex) in selectCatalogue.sections" :key="showIndex">
                <div class="article-list" :class="{'article-list-open': sectionIndex === showIndex}">
                  <p class="article-name"
                     v-for="(article, articleIndex) in getShowArticles(sectionIndex === showIndex, section.articles)"
                     :key="articleIndex"
                     @click="displayArticle(showIndex, articleIndex)">{{ article.name }}</p>
                </div>
                <p v-if="section.articles.length > 3" class="show-more"
                   @click="openSectionOpen(catalogueIndex, showIndex)">
                  <i class="show-more-icon"
                     :class="{'show-more-icon-close': showIndex === sectionIndex, 'show-more-icon-open': showIndex != sectionIndex}"
                  ></i>{{ section.showMoreTip }}</p>
              </div>
            </div>
          </div>
          <div v-if="showType === 1" class="articles-detail">
            <router-view/>
          </div>
          <div v-if="showType === 2" class="search-list" id="search-list-mb">
            <p class="search-result-title">{{ $t('help.Title') }}"{{ searchTextBuf }}"</p>
            <p class="result-count">{{ searchResults.length }} {{ $t('help.Subtitle') }}</p>
            <div class="result-container">
              <div class="result-item" v-for="(result,searchIndex) in searchResults" :key="searchIndex">
                <p class="result-title" @click="displayFromSearch(result)">{{ result.name }}</p>
                <p class="result-detail" v-html="result.snippet" @click="displayFromSearch(result)"></p>
                <p class="result-from">{{ $t('help.Paragraph-1') }}<span
                    @click="openCategory(result.categorieIndex)">{{ result.categorieName }}</span> > <span
                    @click="openSection(result.categorieIndex, result.sectionIndex)">{{ result.sectionName }}</span></p>
              </div>
            </div>
            <p class="no-result-tips">{{ $t('help.Paragraph-2') }}<span class="contact-us"
                                                                        @click="openZw()">{{
                $t('help.Hyperlink')
              }}</span>
            </p>
          </div>
          <div v-if="showType === 3" class="contact-container-box">
            <router-view
                v-bind:channelId="mChannelId"
            />
          </div>
        </div>
        <div class="bottom-box" :class="{'bottom-box-fixed':isFixedBottom}">
          <a class="home-text" @click="gotoHome()">{{ $t('title') }}</a>
          <span>{{ $t('help.Header-1') }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SupportMbHeader from '../components/SupportMbHeader';

export default {
  name: 'support',
  data() {
    return {
      isPc: this.isPc(),
      isLoading: true,
      articles: [],
      sections: [],
      categories: [], // for request buf
      catalogue: [], // for display
      breadcrumb: [],
      iconLoading: false,
      searchText: '',
      searchTextBuf: '',
      selectCatalogue: {},
      catalogueIndex: 0,
      sectionIndex: -1,
      articleBody: '',
      showType: 0, // 0: section list; 1: article; 2: search results
      searchResults: [],
      isFixedBottom: false,
      mChannelId: {},
      intervalId: '',
      isNetWorkError: false,
      isLogin: !!localStorage.getItem('TOKEN'),
      filterCategories: [360002440211, 360002440191, 360002440231, 360002440251, 360002460371, 360000728332, 360002440211]
    };
  },
  components: {
    SupportMbHeader,
  },
  watch: {
    $route(to) {
      if (to.name === 'support') {
        this.showType = 0;
        this.selectCatalogue = this.catalogue[0];
        this.catalogueIndex = 0;
        this.sectionIndex = -1;
      }
    }
  },
  created() {
  },
  mounted() {
    this.loadData()
  },
  methods: {
    loadData() {
      const that = this;
      const result1 = require('../assets/articles/articles.json');
      that.articles = result1.articles.filter(articleItem => {
        return articleItem.title.toUpperCase().indexOf('SSR') === -1
      });
      const result2 = require('../assets/articles/categories.json');
      that.categories = result2.categories.filter(categorigesItem => {
        return that.filterCategories.indexOf(categorigesItem.id) != -1;
      });
      const result = require('../assets/articles/sections.json');
      that.sections = result.sections;
      that.categories.forEach(categoryItem => {
        categoryItem['sections'] = [];
        that.sections.forEach(sectionItem => {
          if (sectionItem['category_id'] === categoryItem['id']) {
            sectionItem['articles'] = [];
            sectionItem['showMoreTip'] = '展開' // by default
            that.articles.forEach(article => {
              if (article['section_id'] === sectionItem['id'] && article['id'] != 360038782191) {
                if (that.isLogin || (!that.isLogin && article['id'] != 360004851731)) {
                  sectionItem['articles'].push(article);
                }
              }
            });
            categoryItem['sections'].push(sectionItem);
          }
        });
      });
      that.catalogue = that.categories;
      that.catalogue.forEach(item => {
        item.sections.forEach(citem => {
          citem['isShow'] = true;
        });
      });
      that.isLoading = false;
      that.breadcrumb = [];
      if (that.$route.path.indexOf('article') > -1) {
        that.showType = 1;
      } else {
        if (that.$route.path.indexOf('contact-us') === -1) {
          that.selectCatalogue = that.catalogue[0];
        }
      }
      if (that.selectCatalogue && that.selectCatalogue.sections !== undefined) {
        that.selectCatalogue.sections.forEach((item, index) => {
          if (index != that.sectionIndex) {
            item.showMoreTip = 'Unfold';
          } else {
            item.showMoreTip = 'Fold';
          }
        });
      }
  },
  countHeight() {
    if (!this.isPc) {
      const that = this;
      setTimeout(function () {
        let screenHeight = document.documentElement.clientHeight || document.body.clientHeight;
        let header = document.getElementById('mb-header').offsetHeight;
        let content = document.getElementById('support-content').offsetHeight;
        that.isFixedBottom = content <= (screenHeight - header);
      }, 800);
    }
  },
  openZw() {

  },
  switchCatalogueMb(index) {
    this.openCategory(index);
    this.countHeight();
  },
  openContact() {
    if (this.showType !== 3) {
      this.catalogueIndex = -1;
      this.showType = 3;
      this.breadcrumb = [];
      this.breadcrumb[0] = this.$t('help.Header-2');
      this.breadcrumb[1] = this.$t('help.Button');
      this.$router.push({
        path: 'contact-us',
        name: 'contact-us',
      });
    }
    this.mChannelId = Object.assign({}, {'channelId': 0}, {});
  },
  getFileName(file, fileList) {
    console.log(file);
    console.log(fileList);
    return false;
  },
  getShowArticles(item, articles) {
    if (this.isPc) {
      if (item.showMoreTip === 'Fold') {
        return articles;
      } else {
        return articles.filter(function (value, index) {
          return index < 3;
        })
      }
    } else {
      return articles;
    }
  },
  openCategory(index) {
    if (!window.location.pathname.endsWith('help')) {
      this.$router.push({
        path: 'help',
        name: 'support'
      });
    }
    const indexBuf = index;
    const that = this;
    setTimeout(function () {
      that.catalogue.forEach((item, index) => {
        if (index === indexBuf) {
          item.sections.forEach((item) => {
            item['isShow'] = true;
          });
        }
      });

      that.selectCatalogue = Object.assign({}, that.catalogue[indexBuf], {});
      that.catalogueIndex = indexBuf;
      that.sectionIndex = -1;
      that.showType = 0;
    }, 10);
    this.countHeight();
  },
  /**
   * Open from catalogueIndex === 0
   */
  openSection(categoryIndex, sectionIndex) {
    if (!this.isPc) {
      if (this.sectionIndex === sectionIndex) {
        this.sectionIndex = -1;
      } else {
        this.sectionIndex = sectionIndex;
      }
    } else {
      if (!window.location.pathname.endsWith('help')) {
        this.$router.push({
          path: 'help',
          name: 'support'
        });
      }

      const categoryIndexBuf = categoryIndex;
      const sectionIndexBuf = sectionIndex;
      const that = this;
      setTimeout(function () {
        that.catalogue.forEach((item, index) => {
          if (index === categoryIndex) {
            item.sections.forEach((citem, index) => {
              citem['isShow'] = sectionIndex === index;
            });
          }
        });
        that.selectCatalogue = JSON.parse(JSON.stringify(that.catalogue[categoryIndexBuf]));
        let showArray = that.selectCatalogue.sections.filter(item => {
          return item.isShow;
        });
        that.selectCatalogue.sections = showArray;
        that.catalogueIndex = categoryIndexBuf;
        that.sectionIndex = sectionIndexBuf;
        that.showType = 0;
      }, 10);
    }
  },
  /**
   * Open from catalogueIndex != 0
   */
  openSectionOpen(catalogueIndex, showIndex) {
    if (this.sectionIndex !== -1) {
      this.selectCatalogue.sections.forEach(item => {
        if (item.showMoreTip === 'Fold') {
          item.showMoreTip = 'Unfold';
        } else {
          item.showMoreTip = 'Fold';
        }
      });
    } else {
      this.selectCatalogue = {};
      let newObj = this.catalogue[catalogueIndex];
      newObj.sections.forEach((item, index) => {
        if (index === showIndex) {
          if (item.showMoreTip === 'Fold') {
            item.showMoreTip = 'Unfold';
          } else {
            item.showMoreTip = 'Fold';
          }
        } else {
          item.showMoreTip = 'Unfold';
        }
      });
      this.selectCatalogue = Object.assign({}, newObj, {});
    }
  },
  displayArticle(sectionIndex, index) {
    let articleInfo = this.selectCatalogue.sections[sectionIndex]['articles'][index];
    let mArticleId = articleInfo['id'];
    if (articleInfo['label_names'] !== undefined && articleInfo['label_names'] !== null && articleInfo['label_names'].length > 0) {
      articleInfo['label_names'].forEach(function (value) {
        mArticleId = mArticleId + "-" + value;
      });
    }
    this.showType = 1;
    this.$router.push({
      path: 'article',
      name: 'article',
      params: {
        articleId: mArticleId,
      }
    });
    this.countHeight();
  },
  displayFromSearch(result) {
    this.showType = 1;
    this.$router.push({
      path: 'article',
      name: 'article',
      params: {
        articleId: result.id,
      }
    });
    this.breadcrumb[1] = result.categorieName;
    this.breadcrumb[2] = result.sectionName;
    this.countHeight();
  },
  gotoHome() {
    this.$router.push({
      path: '',
      name: 'home'
    });
  },
  breadcrumbGoto(index) {
    switch (index) {
      case 0:
        this.showType = 0;
        this.breadcrumb = this.breadcrumb.slice(0, 1);
        this.catalogueIndex = 0;
        this.selectCatalogue = this.catalogue[0];
        break;
      case 1: {
        let p = -1;
        this.catalogue.map((item, index) => {
          if (item.name === this.breadcrumb[1]) {
            p = index;
          }
        });
        if (p !== -1) {
          this.openCategory(p);
        } else if (this.breadcrumb[1] === this.$t('help.Button')) {
          this.mChannelId = Object.assign({}, {'channelId': 0}, {});
        } else {
          this.sectionIndex = 1000;
        }
        this.breadcrumb = this.breadcrumb.slice(0, 2);
        break;
      }
      case 2:
        this.breadcrumb = this.breadcrumb.slice(0, 3);
        break;
    }
    this.countHeight();
  },
  searchKeyword() {
    const that = this;
    that.searchTextBuf = that.searchText;
    this.$request.getSearchKeyword(this.searchText, function (data) {
      that.searchResults = data.data.results;
      that.searchResults.forEach(function (resultItem) {
        that.sections.forEach(function (sectionItem) {
          if (resultItem['section_id'] === sectionItem.id) {
            that.categories.forEach(function (categoriesItem, categorieIndex) {
              if (categoriesItem.id === sectionItem['category_id']) {
                resultItem['categorieName'] = categoriesItem.name;
                resultItem['categorieIndex'] = categorieIndex;
                resultItem['sectionName'] = sectionItem.name;
                resultItem['sectionIndex'] = categoriesItem.sections.indexOf(sectionItem);
              }
            });
          }
        });
      });
      that.showType = 2;
      that.countHeight();
    });
  },
  /*isLogout(val) {
    this.catalogue.forEach(function (value, index) {
          value.sections.forEach(function (value, index) {
              if (value.id === 360001779432){
                  let p = 0;
                  value.articles.forEach(function (value,index) {
                       if (value['id'] === 360004851731) {
                          p = index;
                       }
                  });
                  value.articles.splice(p,1);
              }
          })
    });
    this.selectCatalogue = Object.assign({}, this.catalogue[this.catalogueIndex], {});
  }*/
}
}
;
</script>

<style lang="scss">
.support-container {
  width: 100%;
  height: 100%;
  background-color: #fff;

  .support-container-mb {
    background-color: #fff;
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;

    .support-loading {
      height: 100vh;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      .anticon, .anticon-loading, .ant-spin-dot {
        width: unset;
        height: unset;
      }
    }

    .support-content {
      flex-direction: column;

      .center-content {
        display: flex;
        flex-direction: column;
        margin: 0rem 0.533rem 2.133rem 0.533rem;

        .top-breadcrumb {
          margin: 0.693rem 0px;
        }

        .search-box {
          display: flex;
          flex-direction: row;
          margin: 0.96rem 0;

          .search-content {
            width: 80%;
            height: 1.227rem;
            border: 1px solid #E4EAF0;
            box-shadow: 0 0.027rem 0.04rem rgba(65, 85, 142, 0.16);
            font-size: 0.427rem;
            font-weight: 400;
            text-align: left;
            background: #F7F8FA;
          }

          .ant-btn {
            width: 20%;
            height: 1.227rem;
            font-size: 0.373rem;
            font-weight: 400;
            border-color: #3963EF;
            background: #3963EF url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/search-icon.svg') no-repeat center;
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
          }
        }

        .other-catalogue {
          display: flex;
          flex-direction: column;
          //margin: 0rem 0.533rem 2.133rem 0.533rem;

          .client-title {
            font-size: 0.693rem;
            color: #484652;
            font-weight: 600;
            margin-bottom: 0.213rem;
          }

          .client-description {
            font-size: 0.4rem;
            color: #9DA3B9;
            font-weight: 400;
            margin-bottom: 0.96rem;
          }

          .client-item-container {
            display: flex;
            flex-wrap: wrap;

            .client-item {
              width: 100%;
              height: fit-content;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              margin-bottom: 1.12rem;
              cursor: pointer;

              .title-container {
                display: flex;
                flex-direction: row;
                align-items: center;
                width: 100%;
